import Vue from 'vue';
import { calcSumExpOrIncForMonth, getArrForYear } from "@/scripts/main.js";

const incomeStore = {
	namespaced: true,
	state: {
		currentIncome: {},
		income: {
			2019: {},
			2020: {
				1: {
					category: '',
					currency: "RUB",
					value: 40000,
					commentary: '',
					dataFull: 'Thu Nov 08 2020 10:00:00 GMT+0300 (,  )',
				},
				2: {
					category: '',
					currency: "RUB",
					value: 30000,
					commentary: '',
					dataFull: 'Thu Jan 30 2020 00:00:00 GMT+0300 (,  )',
				},
			},
			2021: {
				1: {
					category: '',
					currency: "RUB",
					value: 40000,
					commentary: '',
					dataFull: 'Thu Nov 11 2021 00:00:00 GMT+0300 (,  )',
				},
				2: {
					category: '',
					currency: "RUB",
					value: 20000,
					commentary: '',
					dataFull: 'Tue Nov 30 2021 00:00:00 GMT+0300 (,  )',
				},
				3: {
					category: '',
					currency: "RUB",
					value: 20000,
					commentary: '',
					dataFull: 'Mon Aug 30 2021 00:00:00 GMT+0300 (,  )',
				},
				4: {
					category: '',
					currency: "RUB",
					value: 30000,
					commentary: '',
					dataFull: 'Mon Nov 08 2021 00:00:00 GMT+0300 (,  )',
				},
				5: {
					category: '',
					currency: "RUB",
					value: 20000,
					commentary: '',
					dataFull: 'Fri Nov 05 2021 00:00:00 GMT+0300 (,  )',
				},
			},
			2022: {},
		},
		optionsCategory: [
			{ text: "", value: "" },
			{ text: "", value: "" },
			{ text: "", value: "" },
		],
		currentYear: (new Date()).getFullYear(),
		currentMonth: (new Date().getMonth() + 1),

	},
	getters: {
		getIncome(state) {
			//       
			return getArrForYear(state.income, state.currentYear, state.currentIncome);
		},
		getIncomeAmount(state, getters) {
			//       
			return calcSumExpOrIncForMonth(getters.getIncome, state.currentMonth);
		},
	},
	mutations: {
		ADD_NEW_INCOME(state, incomeAct) {
			//      
			Vue.set(state.income[new Date(incomeAct.dataFull).getFullYear()], incomeAct.id, incomeAct);
		},

	},
	actions: {
		setNewIncome(context, newIncome) {
			//      ,   id           
			const newIncomePlusId = { ...newIncome, id: Object.values(context.state.income[new Date(newIncome.dataFull).getFullYear()]).length + 1 };
			context.commit('ADD_NEW_INCOME', newIncomePlusId);
		},
	}
};

export default incomeStore;